
#app .side-nav {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 300px;
  color: #364149;
  background: #fafafa;
  border-right: 1px solid rgba(0, 0, 0, 0.07);
}
#app .side-nav li.item {
    height: 40px;
    line-height: 40px;
    padding-left: 20px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.07);
}
#app .side-nav li.item .itemlink {
      display: block;
      text-decoration: none;
      color: #364149;
}
#app .side-nav li.item .itemlink.router-link-active {
        color: #42b983;
}
#app .cBody {
  position: absolute;
  top: 0;
  right: 0;
  left: 300px;
  bottom: 0;
  color: #000;
  background: #fff;
  padding: 20px;
  text-align: center;
}
#app .cBody .topTitle {
    font-size: 30px;
    font-weight: bold;
}
#app .cBody .descrip {
    display: block;
    margin-bottom: 20px;
}



